import  {useState} from 'react'
import Item from './Item'
import {v4 as uuid4} from 'uuid';

export default function Form(){
//data 
    const [dataArr,setDataArr]=useState([
        {txt:" monter en compétences sur Node ",id:uuid4()},
        {txt:" apprendre react ",id:uuid4()},
        {txt:" créer un cluster mongodb ",id:uuid4()},

    ])
    const[stateInput,setStateInput]=useState();
    //suprimer un element 
    const deleteElement = id =>{
        //console.log(id);
        const fillteredState = dataArr.filter(item =>{
            return item.id !== id;
        })
        setDataArr(fillteredState );
    }
    const linkeInput=e=> {
        setStateInput(e);
    }
    const addTodo=e=>{
        e.preventDefault();
        const newArr=[...dataArr]

        const newTodo ={};
        newTodo.txt=stateInput;
        newTodo.id=uuid4();

        newArr.push(newTodo);
        setDataArr(newArr);
        setStateInput('');
    }
    return(
        //affichage
        <div className="m-auto px-4 col-12 col-sm-10 col-lg-6">
            <form onSubmit={ e=>addTodo(e)} className="mb-3">
                <label htmlFor="todo" className="form-label mt-3">Chose à faire </label>
                <input value={stateInput} onInput={e=>linkeInput(e.target.value)}type="text" className="form-control" id="todo"/>
                <button className="mt-2 btn btn-primary d-block">envoyez</button>
            </form>

            <h2>Liste des choses à faire </h2>
                <ul className="list-group">
               {dataArr.map((item,)=>{
                   return(
                       <Item txt={item.txt} key={item.id} id={item.id} delFunc={deleteElement}/>

                   )
               })}
                </ul>
        </div>
    )
}